<template>
    <div>
        <span>
            <div class="custom-select" tabindex="1" ref="selectRef">
                <div class="custom-select-selected">
                    <span v-for="(option, index) of options">
                        <span v-if="option.isSelected">{{option['description']}}</span>
                    </span>
                </div>
                <div class="custom-select-items" v-bind:style="{ 'left': left + 'px'}">
                    <div class="custom-select-item"
                         v-for="(option, index) of options" :key="index">
                        <div class="cell-config" @click="option.isSelected = !option.isSelected">
<!--                            <label></label>-->
                            <input type="checkbox" class="ma-0 vert-mid" v-model="option.isSelected" />
                            <label class="ml-2 vert-mid" >{{ option['description'] }}</label>
                        </div>
                    </div>
                </div>
            </div>
        </span>
    </div>
</template>

<script lang="ts">
import {defineComponent, ref} from "vue";


export default defineComponent({
    name: "CustomSelect",
    data() {
        return {
            options: [],
            left: 0,
            selected: []
        }
    },
    mounted() {
        this.left = (this.$refs as any).selectRef.getClientRects()[0].left;
        this.options = this.$store.state.LookUp.workTypes;

    },
    methods: {

    }

})
</script>

<style lang="scss" scoped>
.custom-select-selected:after {
    top: calc(50% - 3px);
    right: 10px;
}

.custom-select-selected {
    padding: 8px 10px;
    min-height: 35px;

    //padding: 8px 10px;
    //min-height: 35px;
    max-width: 141px;
    //overflow: hidden;
    max-height: 35px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

}

.custom-select-items {
    position: fixed;
    width: 150px;
}

.custom-select-empty-selected {
    display: inline-block;
    width: 1px;
    height: 15px;
}

.cell-config {
    label {
        cursor: pointer;
    }
}

.vert-mid {
    vertical-align: middle;
}
</style>