
import {ScheduleDay} from "@/components/FlexSchedule/schedule-day-mock";
import {defineComponent} from "vue";

export default defineComponent({
    name: "PrintableView",
    props: {
        scheduleWeek: {
            type: Object
        },
        weekDays: {
            type: Array
        }
    },
    methods: {
        getTotalHour(sche: ScheduleDay[]) {
            let totalHours = 0;
            for (let day of sche) {
                if (day.hours) {
                    totalHours += day.hours
                }
            }
            return totalHours.toFixed(2)
        },

        printFlexSchedule() {
            var args = "left=5,top=5,resizable=yes,height=600,width=700,scrollbars=yes,status=1";
            var content = (document as any).getElementById("FlexSchedules").outerHTML;
            var mywindow: any = window.open('', 'Print', args);

            // https://stackoverflow.com/questions/65569790/import-scss-as-string
            const printStyle = require("!!css-loader!sass-loader!./PrintableView.scss").toString();

            mywindow.document.write('<html>');
            mywindow.document.write('<head>');
            mywindow.document.write('<style type="text/css">table {border-collapse: collapse;}</style>');
            mywindow.document.write('<style type="text/css">table, th, td {border: 1px solid #cbcbcb;text-align: center;}</style>');
            mywindow.document.write(`<style type="text/css">${printStyle}</style>`);
            mywindow.document.write('</head>');
            mywindow.document.write('<body>');
            mywindow.document.write(content);
            mywindow.document.write('</body></html>');

            mywindow.document.close();
            mywindow.focus()
            mywindow.print();
            mywindow.close();
        }
    }
})
